import React from "react";
import { withRouter } from "react-router-dom";
import Avatar from "components/Avatar";
import { Row, DropdownItem, DropdownMenu, DropdownToggle, Nav, UncontrolledDropdown} from "reactstrap";
import moment from 'moment';
import Auth from "Auth";

const ChatHeader = props => {

  const status = () => {
    if(props.typing) return ' ';
    if(props.contact.status === true) return ' ';
    if(props.contact.status) return moment(props.contact.status).fromNow();
  };

  return (
    <Row className="heading m-0">
      <div onClick={props.toggle}>
        <Avatar src={props.contact.avatar} />
      </div>
      <div className="text-right">
        <div>{props.contact ? props.contact.name : ''}</div>
        <small>{status()}</small>
      </div>
      <Nav className="mr-auto" navbar>
        <UncontrolledDropdown>
          <DropdownToggle tag="a" className="nav-link">
            <i className="fa fa-ellipsis-v" />
          </DropdownToggle>
          <DropdownMenu>
            <DropdownItem onClick={e => props.history.push('/password')}>  </DropdownItem>
            <DropdownItem divider />
            <DropdownItem onClick={props.logout}> </DropdownItem>
          </DropdownMenu>
        </UncontrolledDropdown>
      </Nav>
    </Row>
  );
};

export default withRouter(ChatHeader);